    time,
    message,
    type
  })
  
  // ÈôêÂà∂Êó•ÂøóÊï∞Èáè
  if (logs.value.length > 50) {
    logs.value.pop()
  }
}

// ËøêË°åÂÆåÊï¥ÊµãËØï
const runFullTest = async () => {
  addLog('ÂºÄÂßãËøêË°åÂÆåÊï¥ÊµãËØïÂ•ó‰ª∂...', 'info')
  
  try {
    await AuthTestHelper.runFullTest()
    addLog('ÂÆåÊï¥ÊµãËØïÂ•ó‰ª∂ÊâßË°åÂÆåÊàê', 'success')
  } catch (error) {
    addLog(`ÂÆåÊï¥ÊµãËØïÂ§±Ë¥•: ${error.message}`, 'error')
  }
}

// Ê®°ÊãüÁôªÂΩï
const simulateLogin = async () => {
  addLog('ÂºÄÂßãÊ®°ÊãüÁôªÂΩïÊµãËØï...', 'info')
  
  try {
    const success = await AuthTestHelper.simulateLogin()
    if (success) {
