// ÊµãËØïTokenÂà∑Êñ∞
const testTokenRefresh = async () => {
  addLog('ÂºÄÂßãÊµãËØïTokenÂà∑Êñ∞Êú∫Âà∂...', 'info')
  
  try {
    if (!authStore.isAuthenticated) {
      addLog('ËØ∑ÂÖàÁôªÂΩïÂêéÂÜçÊµãËØïTokenÂà∑Êñ∞', 'warning')
      return
