  addLog('ÂºÄÂßãÊµãËØïÊùÉÈôêÊ£ÄÊü•ÂäüËÉΩ...', 'info')
  
  try {
    // ÊµãËØïÊñá‰ª∂‰∏ä‰º†ÊùÉÈôê
